import React, { useState } from 'react';
import { Button } from '@material-ui/core';
import { storage, db } from './firebase';
import firebase from 'firebase';
import { makeStyles } from '@material-ui/core/styles';
import LinearProgress from '@material-ui/core/LinearProgress';




const useStyles = makeStyles((theme) => ({
descriptionArticle:{
      textAlign:'center',  
      color:'#00895e',  
      height:70,
      width:300,  
      outline:'none',
      backgroundColor:'#00895',
      borderRadius:'10px',
      border:'1px',
      margin:2,
      backgroundColor:'none',
      backgroundColor:'none',
    },
    selectButton:{
        color:'#00895e',
        width:390, 
        outline:'none',
    },    
progressBar:{
      backgroundColor:'transparent',

},
publicarButton:{
    backgroundColor:'#2e9b6c'
}
  }));
const ImageUpload = (username) => {
const classes = useStyles();


    const [image, setImage] = useState(null);
    const [progress, setProgress] = useState(0);
    const [caption, setCaption] = useState('');

    const handleChange = (e) =>{
        if(e.target.files[0]) {
            setImage(e.target.files[0]);
        }
    };

    const handleUpload = () =>{
        const uploadTask = storage.ref(`images/${image.name}`).put(image);

        uploadTask.on(
            'state_changed',
            (snapshot) => {
                //progress function
                const progress = Math.round(
                    (snapshot.bytesTransferred / snapshot.totalBytes) *100
                );
                setProgress(progress);
            },
            (error) =>{
                //error function
                console.log(error.message);
                alert(error.message);
            },
            () =>{
                //complete function
                storage
                .ref('images')
                .child(image.name)
                .getDownloadURL()
                .then(url =>{
                    //post image inside db
                    db.collection('posts').add({
                        timestamp: firebase.firestore.FieldValue.serverTimestamp(),
                        caption: caption,
                        imageUrl: url,
                        username: username
                    });

                    setProgress(0);
                    setCaption('');
                    setImage(null);
                });
            }
        )
    }
 
    return (
        <div>



<div className={classes.root}>
    
      <LinearProgress className={classes.progressBar} variant="determinate" value={progress} />
      <LinearProgress className={classes.progressBar} variant="determinate" value={progress} />
      

    </div>
    

            {/* <progress value={progress} max='100' /> */}
            <input className={classes.descriptionArticle} type="text" placeholder='Agrega una descripción de lo que quieres vender' onChange={event => setCaption(event.target.value)} value={caption} />
            <input className={classes.selectButton} type="file" onChange={handleChange} />
            <Button className={classes.publicarButton} onClick={handleUpload} > Publicar </Button>
        </div>
    );
};

export default ImageUpload;